import React from "react";
// import * as Fui from "@fluentui/react";
import { RouteComponentProps, withRouter } from "react-router-dom";
import { RoutedComponentBase } from "../Base/ComponentBase";
import { Budget } from "./Issues/Budget";
import { EconomicDevelopment } from "./Issues/EconomicDevelopment";

class PrivateIssues extends RoutedComponentBase {
	public constructor(props: RouteComponentProps) {
		super(props);
	}

	private renderInside(): React.ReactNode {
		switch (this.props.match.params.issueId) {
			case "focus-on-core":
				return <Budget {...this.props} />;
			case "economic-development":
				return <EconomicDevelopment {...this.props} />;
			default:
				return this.props.match.params.issueId;
		}
	}

	public render(): React.ReactNode {
		return <div className="centered-container">{this.renderInside()}</div>;
	}
}

export const Issues = withRouter(PrivateIssues);
