import React from "react";
import * as Fui from "@fluentui/react";
import { ComponentBase } from "../Base/ComponentBase";
import header from "../assets/brian-horse-cropped.jpeg";
import footer from "../assets/slogan.svg";
import { TwoColumns } from "./TwoColumns";

export class Splash extends ComponentBase {
	private readonly CONTRIBUTE_TO_CAMPAIGN_TEXT = "Contribute to the Campaign";
	private readonly BRIAN_FOR_COUNCIL = "Brian Alg For College Station City Council";
	private renderLargeScreen(): React.ReactNode {
		return (
			<React.Fragment>
				<TwoColumns
					className="banner-blue"
					widths={{
						lg: { column1: "6", column2: "6" },
					}}
					column1={<div
						className="banner-blue"
						style={{
							minHeight: "55vh",
							backgroundImage: `url(${header})`,
							backgroundRepeat: "no-repeat",
							backgroundPosition: "10%",
							backgroundSize: "contain",
							margin: "10px",
						}}
					/>}
					column2={
						<div className="centered-container" style={{ minHeight: "50vh", color: "white" }}>
							<div style={{ padding: "50px 0px" }}>
								<Fui.Button
									style={{ width: "25vw", height: "10vh" }}
									href={this.DONATION_LINK}
									styles={{
										root: {
											fontSize: "2.5vh",
										},
									}}
								>
									{this.CONTRIBUTE_TO_CAMPAIGN_TEXT}
								</Fui.Button>
							</div>
							<div style={{ padding: "50px 0px" }}>
								<Fui.Text variant="xxLargePlus">{this.BRIAN_FOR_COUNCIL}</Fui.Text>
							</div>
						</div>
					}
				/>
				<div className="centered-container" style={{ minHeight: "10vh" }}>
					{/* <Fui.Text variant="xxLargePlus">{this.BRIAN_FOR_COUNCIL}</Fui.Text> */}
				</div>
				<div
					className="banner-red"
					style={{
						minHeight: "18vh",
						maxHeight: "18vh",
						backgroundImage: `url(${footer})`,
						backgroundRepeat: "no-repeat",
						backgroundPosition: "center",
						backgroundSize: "contain",
					}}
				/>
			</React.Fragment>
		);
	}

	private renderSmallScreen(): React.ReactNode {
		return (
			<React.Fragment>
				<div className="banner-blue">
					<div
						style={{
							minHeight: "40vh",
							backgroundImage: `url(${header})`,
							backgroundRepeat: "no-repeat",
							backgroundPosition: "10%",
							backgroundSize: "contain",
							margin: "10px",
						}}
					/>
					<div style={{ display: "flex", flexDirection: "column", alignItems: "center", justifyContent: "center", minHeight: "15vh" }}>
						<Fui.Button
							style={{ width: "80vw", height: "10vh" }}
							href={this.DONATION_LINK}
							styles={{
								root: {
									fontSize: "2.5vh",
								},
							}}
						>
							{this.CONTRIBUTE_TO_CAMPAIGN_TEXT}
						</Fui.Button>
					</div>
				</div>
				<div style={{ display: "flex", flexDirection: "column", alignItems: "center", justifyContent: "center", minHeight: "13vh" }}>
					<Fui.Text variant="large">{this.BRIAN_FOR_COUNCIL}</Fui.Text>
				</div>
				<div
					className="banner-red"
					style={{
						minHeight: "19vh",
						maxHeight: "19vh",
						backgroundImage: `url(${footer})`,
						backgroundRepeat: "no-repeat",
						backgroundPosition: "center",
						backgroundSize: "contain",
					}}
				/>
			</React.Fragment>
		);
	}

	public render(): React.ReactNode {
		return (
			<React.Fragment>
				<div className="ms-Grid">
					<div className="ms-Grid-row ms-hiddenMdDown">{this.renderLargeScreen()}</div>
					<div className="ms-Grid-row ms-hiddenLgUp" style={{ minHeight: "60vh" }}>{this.renderSmallScreen()}</div>
				</div>
			</React.Fragment>
		);
	}
}
