import { Text } from "@fluentui/react";
import React from "react";
import { RoutedComponentBase } from "../Base/ComponentBase";
import BrianImg1 from "../assets/brian1.jpg";
import { TwoColumns } from "./TwoColumns";
import { Typography } from "./Typography";

export class Bio extends RoutedComponentBase {
	public render(): React.ReactNode {
		return (
			<TwoColumns
				column1={
					<div style={{
						backgroundImage: `url(${BrianImg1})`,
						backgroundRepeat: "no-repeat",
						backgroundSize: "contain",
						minHeight: "60vh",
					}} />
				}
				column2={[
					<p key={1}>
						<Typography>
							I am Brian Alg and I am running for Place 5 on College Station's City Council.
							All incumbents running have a record of disregard for the liberty and property of the citizens of College Station.
							John Nichols is no exception. His record and campaign statements make it clear he intends to use the current downturn
							as an excuse to continue to push for pet projects and disregard the rights of citizens.
						</Typography>
					</p>,
					<p key={2}>
						<Typography>
							We do not need leaders with big plans for what they want to do with <Text variant="mediumPlus">your</Text> stuff.
						</Typography>
					</p>,
					<p key={3}>
						<Typography>
							We can do better.
						</Typography>
					</p>,
					<p key={4}>
						<Typography>
							For sure, we are facing some hard times this year. Across the country, budgets are tightening. It is now more imperative than ever that we focus city resources on the core responsibilities. I see an opportunity to cut expensive extraneous activity from the budget. This should have been done years ago.
						</Typography>
					</p>,
					<p key={5}>
						<Typography>
							Now is a great time to correct past mistakes.
						</Typography>
					</p>,
					<p key={6}>
						<Typography>
							I am confident that coming out of this difficult time we could be entering a new golden age in College Station. We have so many things going for us. And I am excited to see what we can do with a city government that focuses on its job of providing excellent core services and otherwise letting citizens do great things with more of their own money. I will be happy to make that happen as your city councilman.
						</Typography>
					</p>,
				]}
			/>
		);
	}

}
