import React from "react";
import { RoutedComponentBase } from "../Base/ComponentBase";
import { TwoColumns } from "./TwoColumns";
import BrianJpg from "../assets/brian2.jpg";
import { Typography } from "./Typography";

export class Social extends RoutedComponentBase {
	public render(): React.ReactNode {
		return (
			<TwoColumns
				column1={<Typography>Col1</Typography>}
				column2={
					<div style={{
						backgroundImage: `url(${BrianJpg})`,
						backgroundRepeat: "no-repeat",
						backgroundSize: "contain",
						minHeight: "30vh",
					}} />
				}
			/>
		);
	}
}
