import React from "react";
import { RoutedComponentBase } from "../../Base/ComponentBase";
import { Typography } from "../Typography";

export class EconomicDevelopment extends RoutedComponentBase {
	public render(): React.ReactNode {
		return (
			<React.Fragment>
				<Typography variant="heading">
					Sensible economic development
				</Typography>
				<Typography variant="subheading">
					Bring on the golden age!
				</Typography>
				<Typography halfWidth={true}>
					It shocks me when a city council sends a major project away with the expectation that they will come back with a new plan less likely to work,
					but more to the council's liking.
					How much BS do these folks think people will put up with?
					Multi-month delays cost a fortune.
					These are just the massive projects with big budgets.
				</Typography>
				<Typography variant="emphasize">
					How many small businesses never get off the ground because they don't have the time and money to play these games?
				</Typography>
				<Typography halfWidth={true}>
					It should not be standard procedure to have to go through the economic development department to negotiate with the planning and
					zoning department to get a variance on a parking quota that was established by somebody who knows nothing about the needs of your business.
				</Typography>
				<Typography halfWidth={true}>
					We need to end this situation: people who run their business well are being forced to run their business poorly by people who don’t know how to run a business at all.
				</Typography>
				<Typography halfWidth={true}>
					A city with clear and reasonable requirements
					(not unnecessary and burdensome color palette approvals, parking quotas, and shrubbery requirements)
					will attract sensible investment in a way that millions of taxpayer dollars in giveaways never could.
				</Typography>
				<Typography halfWidth={true}>
					As a bonus, an even playing field with clear and reasonable rules will be fairer to the thousands of businesses that do not have special connections to the city government and must pay for those economic development subsidies.
				</Typography>
				<Typography variant="emphasize">
					In the new proposed budget, the city is slated to dole out $650,000 in cash assistance.
				</Typography>
				<Typography halfWidth={true}>
					Last year it was $900,000.
					No doubt, there will be calls by others to provide more giveaways as our economy recovers.
				</Typography>
				<Typography halfWidth={true}>
					Unless you are one of the select few that the city has committed to giving preferential treatment at the expense of everyone else,
					a level playing field is the best policy for you.
					We don't need central planning - where the city government gives subsidies and regulatory giveaways to their favorite residents/businesses then over-tax and over-regulate everyone else
					(possibly forcing them out of town or out of business).
				</Typography>
				<Typography halfWidth={true}>
					We need to chart a better course so all businesses, especially small businesses,
					have the agility they need to find their way through this transforming economy.
				</Typography>
			</React.Fragment>
		);
	}
}
