import React from "react";
import * as RRD from "react-router-dom";
import { Fabric, Text, Link } from "@fluentui/react";
import { NavigationBar } from "./Components/NavigationBar";
import { ComponentBase } from "./Base/ComponentBase";
import { Routes } from "./Base/Routes";
import { map } from "lodash";
import "office-ui-fabric-react/dist/css/fabric.css";

export class App extends ComponentBase {
	public constructor(props: {}) {
		super(props);
	}

	public render(): React.ReactNode {
		return (
			<div className="app">
				<Fabric>
					<NavigationBar />
					<main>
						<RRD.BrowserRouter>
							<RRD.Switch>
								{
									map(Routes, (routeDef, route) => {
										// console.log(`Regisering ${route} @ "${routeDef.url}"`);
										return <RRD.Route key={routeDef.url} path={routeDef.url} exact={!routeDef.notExact} component={routeDef.component} render={routeDef.render} />;
									})
								}
							</RRD.Switch>
						</RRD.BrowserRouter>
						<div className="centered-container">
							<Text variant="small" style={{ paddingTop: "5px" }}>Paid for by Brian Alg. Design by <Link href="https://rezateshnizi.com/" target="_blank">Reza Teshnizi</Link></Text>
						</div>
					</main>
				</Fabric>
			</div>
		);
	}
}
