import React from "react";
import { IFontStyles, Text } from "@fluentui/react";
import { ComponentBase } from "../Base/ComponentBase";

type TextVariant = "normal" | "subheading" | "heading" | "emphasize";

interface Props {
	variant?: TextVariant;
	halfWidth?: boolean;
	paragraph?: boolean;
}

export class Typography extends ComponentBase<Props> {
	private renderText(variant: keyof IFontStyles, emphasize?: true): React.ReactNode {
		const text = emphasize ? <i>{this.props.children}</i> : this.props.children;
		const textEl = <Text variant={variant}>{text}</Text>;
		if (this.props.halfWidth) {
			return <p style={{ width: "50%", height: "auto" }}>{textEl}</p>;
		}
		if (this.props.paragraph) {
			return <p>{textEl}</p>;
		}
		return textEl;
	}

	public render(): React.ReactNode {
		switch (this.props.variant) {
			case "normal":
				return this.renderText("large");
			case "emphasize":
				return this.renderText("xLarge", true);
			case "subheading":
				return this.renderText("xxLarge");
			case "heading":
				return this.renderText("mega");
			default:
				return this.renderText("large");
		}
	}
}
