import React from "react";
import { RoutedComponentBase } from "../../Base/ComponentBase";
import { Typography } from "../Typography";

export class Budget extends RoutedComponentBase {
	public render(): React.ReactNode {
		return (
			<React.Fragment>
				<Typography variant="heading">
					The budget: focus on the core, cut the kickball
				</Typography>
				<p>
					<Typography variant="subheading">
						Over the last 8 years, the tax rate has increased 24%.
						This needs to stop.
						This can be stopped!
					</Typography>
				</p>
				<p style={{ width: "50%", height: "65vh" }}>
					<Typography variant="normal">
						The city’s budget is full of spending on things that should not be the city’s business (for example, Arts Council and adult kickball leagues).
						These, as well as the proposed bird blinds and multi million dollar private YMCA clubs, are projects for organizations like the United Way not government.
						People generally expect their taxes to go to a handful of core services, primarily public safety (police and fire) and infrastructure (roads and utilities).
						If the city government commits itself to providing these services instead of seeing itself as a municipal Santa Claus, we will be able to cut the budget substantially and still provide the things that reasonable people expect from their city government.
					</Typography>
				</p>
			</React.Fragment>
		);
	}
}
