import React from "react";
import * as Fui from "@fluentui/react";
import * as FuiIcons from "@fluentui/react-icons";
import { ComponentBase, Issues } from "../Base/ComponentBase";
import name from "../assets/name.svg";
import logo from "../assets/logo.svg";
import AlgTheme from "../Base/Theme";
import { isString, map } from "lodash";

interface ItemOptions {
	key: string;
	url: string;
	label?: string;
	iconOnly?: boolean;
	iconName?: keyof typeof myIcons;
	buttonStyles?: Fui.IButtonStyles;
	minWidth?: string;
	backgroundImage?: string;
	backgroundRepeat?: string;
	backgroundSize?: string;
	backgroundPosition?: "center";
}

const myIcons = {
	facebook: <FuiIcons.FacebookLogoIcon />,
};

Fui.registerIcons({ icons: myIcons });

export class NavigationBar extends ComponentBase {
	private readonly ICON_SIZE = "44px";
	private createItem(arg: string | ItemOptions): Fui.ICommandBarItemProps {
		let key = "";
		let text = "";
		let url = "";
		let iconOnly: boolean | undefined = false;
		let iconName: keyof typeof myIcons | "" = "";
		let buttonStyles: Fui.IButtonStyles = {
			root: {
				backgroundColor: AlgTheme.palette.themePrimary,
				color: AlgTheme.palette.white,
				padding: "0 10px",
				minWidth: (typeof arg === "string") ? "" : arg.minWidth,
				backgroundImage: (typeof arg === "string") ? "" : arg.backgroundImage,
				backgroundRepeat: (typeof arg === "string") ? "" : arg.backgroundRepeat,
				backgroundSize: (typeof arg === "string") ? "" : arg.backgroundSize,
				backgroundPosition: (typeof arg === "string") ? "" : arg.backgroundPosition,
			},
			rootHovered: {
				backgroundColor: AlgTheme.palette.themeSecondary,
				color: AlgTheme.palette.neutralLighter,
			},
			rootPressed: {
				backgroundColor: AlgTheme.palette.themeTertiary,
				color: AlgTheme.palette.neutralLighter,
			},
			menuIcon: {
				color: AlgTheme.palette.white,
			},
			menuIconHovered: {
				color: AlgTheme.palette.neutralLighter,
			},
			menuIconPressed: {
				color: AlgTheme.palette.neutralLighter,
			},
		};
		if (isString(arg)) {
			key = arg;
			text = arg;
		} else {
			key = (arg as ItemOptions).key;
			text = (arg as ItemOptions).label || "";
			url = (arg as ItemOptions).url;
			iconOnly = (arg as ItemOptions).iconOnly;
			iconName = (arg as ItemOptions).iconName || "";
			buttonStyles = (arg as ItemOptions).buttonStyles || buttonStyles;
		}

		return {
			key,
			text,
			cacheKey: key, // changing this key will invalidate this item's cache
			buttonStyles,
			iconOnly,
			iconProps: iconName ? { iconName, styles: { root: { color: "white", fontSize: "2.5vw", height: "auto" } } } : undefined,
			href: url,
		};
	}

	private logo(): Fui.ICommandBarItemProps {
		const sizeLogo = window.innerWidth < 600 ? logo : name;
		return this.createItem({
			key: "logo",
			url: "/",
			minWidth: "10vw",
			backgroundImage: `url(${sizeLogo})`,
			backgroundRepeat: "no-repeat",
			backgroundSize: "contain",
			backgroundPosition: "center",
		});
		// return {
		// 	key: "logo",
		// 	buttonStyles: {
		// 		root: {
		// 			minHeight: this.ICON_SIZE,
		// 			minWidth: this.ICON_SIZE,
		// 			backgroundImage: `url(${logo})`,
		// 			backgroundRepeat: "no-repeat",
		// 			backgroundSize: "contain",
		// 		},
		// 	},
		// 	cacheKey: "logo", // changing this key will invalidate this item's cache
		// };
	}

	private generateIssues(): Fui.IContextualMenuItem[] {
		return map(Issues, (issueName, issueId) => ({
			key: issueName,
			text: issueName,
			href: `/issues/${issueId}`,
		}));
	}

	private issues(): Fui.ICommandBarItemProps {
		return {
			...this.createItem("Issues"),
			subMenuProps: {
				items: this.generateIssues(),
			},
		};
	}

	private contribute(): Fui.ICommandBarItemProps {
		return this.createItem({
			key: "contribute",
			label: "Contribute",
			url: this.DONATION_LINK,
			buttonStyles: {
				root: {
					backgroundColor: AlgTheme.palette.redDark,
					color: AlgTheme.palette.white,
					padding: "0 10px",
				},
				rootHovered: {
					backgroundColor: AlgTheme.palette.red,
					color: AlgTheme.palette.neutralLighter,
				},
				rootPressed: {
					backgroundColor: AlgTheme.palette.themeSecondary,
					color: AlgTheme.palette.neutralLighter,
				},
			},
		});
	}

	private items(): Fui.ICommandBarItemProps[] {
		const items: Fui.ICommandBarItemProps[] = [
			this.logo(),
			// this.createItem("About Brian"),
			this.issues(),
			this.contribute(),
		];
		return items;
	}

	private socialLinks(): Fui.ICommandBarItemProps[] {
		const items: Fui.ICommandBarItemProps[] = [
			this.createItem({
				key: "facebook",
				url: "https://www.facebook.com/Vote4Alg",
				iconOnly: true,
				iconName: "facebook",
			}),
		];
		return items;
	}

	public render(): React.ReactNode {
		return (
			<Fui.CommandBar
				items={this.items()}
				farItems={this.socialLinks()}
				styles={{
					root: {
						backgroundColor: AlgTheme.palette.themePrimary,
						height: "10vh",
					},
				}}
			/>
		);
	}
}
