import React from "react";
import { RoutedComponentBase } from "../Base/ComponentBase";
import { Bio } from "./Bio";
// import * as RRD from "react-router-dom";
import { Social } from "./Social";

export class Home extends RoutedComponentBase {
	public render(): React.ReactNode {
		return (
			<React.Fragment>
				<Bio {...this.props} />
				<Social {...this.props} />
			</React.Fragment>
		);
	}
}
